import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SingleApplicationRoutingModule } from './single-application-routing.module';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AngularMaterialModule } from '@shared';
import { MatTableModule } from '@angular/material/table';
import { ModalSingleApplicationComponent } from './modal-single-application/modal-single-application.component';


@NgModule({
  declarations: [ModalSingleApplicationComponent],
  imports: [
    CommonModule,
    SingleApplicationRoutingModule,
    HttpClientModule,
    ReactiveFormsModule,
    FormsModule,
    AngularMaterialModule,
    MatTableModule
  ]
})
export class SingleApplicationModule { }
